























































































































































































































































































































import Vue from "vue";
export default Vue.extend({
  name: "Resume",
  methods: {
    scrollTo(id: string) {
      const el = document.getElementById(id);
      if (el) {
        // Ensure headings don't hide under fixed headers (if any)
        el.scrollIntoView({ behavior: "smooth", block: "start" });
      }
    },
  },
});

